import React from "react";
import { SessionPlanInterface } from "../../../interfaces";
import {
  WaitingListDataInterface,
  WaitingListStatus,
} from "../../../interfaces/waitingList";
import { getPeriodById } from "../../Admin/ExamReport/ExamReport";
import VBarChart from "../ChartComponents/VBarChart";

type WaitingListChartProps = {
  waitingListData: WaitingListDataInterface[];
  sessions: SessionPlanInterface[];
  thisGender: "all" | "male" | "female";
  chartHeight?: number;
};

const WaitingListChart: React.FC<WaitingListChartProps> = (props) => {
  const getDistinctSession = (
    waitingListData: WaitingListDataInterface[]
  ): { sessionNames: string[]; sessionIds: string[] } => {
    let names: string[] = [],
      ids: string[] = [];

    for (const key in waitingListData) {
      if (Object.prototype.hasOwnProperty.call(waitingListData, key)) {
        if (!ids.includes(waitingListData[key].waitingList_session_id)) {
          ids.push(waitingListData[key].waitingList_session_id);
          names.push(waitingListData[key].waitingList_session);
        }
      }
    }

    return { sessionNames: names, sessionIds: ids };
  };

  const FC_GetWaitingListChartData = (): {
    waitingData: number[];
    placementData: number[];
    failedData: number[];
    labels: string[];
  } => {
    let waitingDataArray: number[] = [],
      placementDataArray: number[] = [],
      failedDataArray: number[] = [],
      labelArray: string[] = [],
      labelIds: string[] = [];

    const thisSessions = getDistinctSession(props.waitingListData);
    thisSessions.sessionIds.forEach((thisSessionsKey) => {
      let _waitingListData = props.waitingListData.filter(
        (item) => item.waitingList_session_id === thisSessionsKey
      );
      for (const key in _waitingListData) {
        if (Object.prototype.hasOwnProperty.call(_waitingListData, key)) {
          const element = _waitingListData[key];
          const thisPeriod = getPeriodById(
            props.sessions,
            element.waitingList_session_id
          );
          if (!labelIds.includes(element.waitingList_session_id)) {
            labelIds.push(element.waitingList_session_id);
            labelArray.push(
              thisPeriod ? thisPeriod.session_plan_name : "Unknown"
            );
            if (props.thisGender !== "all") {
              waitingDataArray.push(
                _waitingListData.filter(
                  (item) =>
                    item.waitingList_session_id ===
                      element.waitingList_session_id &&
                    item.waiting_status === WaitingListStatus.WAITING &&
                    item.gender.toString().toLocaleLowerCase() ===
                      props.thisGender.toString().toLocaleLowerCase()
                ).length
              );
              placementDataArray.push(
                _waitingListData.filter(
                  (item) =>
                    item.waitingList_session_id ===
                      element.waitingList_session_id &&
                    item.waiting_status === WaitingListStatus.PLACED &&
                    item.gender.toString().toLocaleLowerCase() ===
                      props.thisGender.toString().toLocaleLowerCase()
                ).length
              );
              failedDataArray.push(
                _waitingListData.filter(
                  (item) =>
                    item.waitingList_session_id ===
                      element.waitingList_session_id &&
                    item.waiting_status === WaitingListStatus.PENDING &&
                    item.gender.toString().toLocaleLowerCase() ===
                      props.thisGender.toString().toLocaleLowerCase()
                ).length
              );
            } else {
              waitingDataArray.push(
                _waitingListData.filter(
                  (item) =>
                    item.waitingList_session_id ===
                      element.waitingList_session_id &&
                    item.waiting_status === WaitingListStatus.WAITING
                ).length
              );
              placementDataArray.push(
                _waitingListData.filter(
                  (item) =>
                    item.waitingList_session_id ===
                      element.waitingList_session_id &&
                    item.waiting_status === WaitingListStatus.PLACED
                ).length
              );
              failedDataArray.push(
                _waitingListData.filter(
                  (item) =>
                    item.waitingList_session_id ===
                      element.waitingList_session_id &&
                    item.waiting_status === WaitingListStatus.PENDING
                ).length
              );
            }
          }
        }
      }
    });

    return {
      waitingData: waitingDataArray,
      placementData: placementDataArray,
      failedData: failedDataArray,
      labels: labelArray,
    };
  };

  return (
    <section className="my-3 max-w-full bg-white">
      <div className="flex items-center justify-between px-3">
        <span className="text-gray-600 font-semibold text-xl py-4 px-0">
          Waiting list chart
        </span>
      </div>
      <div className="mb-4 pb-2 px-3">
        <VBarChart
          labels={FC_GetWaitingListChartData().labels}
          categoriesLabel={FC_GetWaitingListChartData().labels}
          dataLabels={false}
          yaxisText="(Applicants)"
          tooltipText="Applicants"
          series={[
            {
              name: "Waiting list",
              data: FC_GetWaitingListChartData().waitingData,
            },
            {
              name: "Pending placement",
              data: FC_GetWaitingListChartData().failedData,
            },
            {
              name: "Confirmed placement",
              data: FC_GetWaitingListChartData().placementData,
            },
          ]}
          colors={["#469769", "#d97724", "#4b5563"]}
          defaultBar={{
            borderRadius: 4,
            horizontal: false,
            columnWidth: "95%",
            endingShape: "rounded",
          }}
          chartHeight={props.chartHeight}
        />
      </div>
    </section>
  );
};

export default React.memo(WaitingListChart);
