import React, { Component } from "react";
import { FaBookOpen } from "react-icons/fa";
import { IoIosBriefcase } from "react-icons/io";
import { IoFilter } from "react-icons/io5";
import { MdSearch } from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  schoolLevelsStoreInterface,
  loadEducationCategories,
  PushPopupAlert,
} from "../../../actions";
import { GetAllSessionPlans } from "../../../actions/session";
import { FC_GetPositionsByInstitution } from "../../../actions/settings";
import { getWaitingList } from "../../../actions/waitingList";
import { PopupAlertData } from "../../../components/Alert/Popup";
import LoadingV2, { LoadingType } from "../../../components/Loading/LoadingV2";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import {
  SessionPlanInterface,
  PositionAPI,
  education_category,
} from "../../../interfaces";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import {
  applicantGender,
  WaitingListDataInterface,
  WaitingListInterface,
  WaitingListStatus,
} from "../../../interfaces/waitingList";
import { StoreState } from "../../../reducers";
import { getPositionById } from "../../../shared/dataTest";
import { DATE, search } from "../../../utils/functions";
import { getPeriodById } from "../../Admin/WaitingList/WaitingListReport";
import NoSession from "../../SessionSettings/NoSession";
import LoadingLabels from "../ChartComponents/LoadingLabels";
import PieChart from "../ChartComponents/PieChart";
import ExamDashboardLabels from "../ExamDashboard/ExamDashboardLabels";
import WaitingListBoardTitle from "./WaitingListBoardTitle";
import WaitingListChart from "./WaitingListChart";

export type WaitingListChartDataInterface = {
  title: string;
  count: {
    all: WaitingListDataInterface[];
    waiting: WaitingListDataInterface[];
    placed: WaitingListDataInterface[];
    failed: WaitingListDataInterface[];
  };
  type: "male" | "female" | "all";
};
interface WaitingListProps {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  systemPositions: PositionAPI[] | null;
  schoolLevels: schoolLevelsStoreInterface;
  waitingList: WaitingListInterface[] | null;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  getWaitingList: Function;
  FC_GetPositionsByInstitution: Function;
  GetAllSessionPlans: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface WaitingListState {
  thisPeriod: string;
  loading: boolean;
  loadingWaitingList: boolean;
  thisPosition: PositionAPI | null;
  selectPosition: boolean;
  thisEducation: education_category | "all";
  thisGender: "all" | "male" | "female";
  searchPositionInput: string;
}

class _WaitingListDashboard extends Component<
  WaitingListProps,
  WaitingListState
> {
  constructor(props: WaitingListProps) {
    super(props);

    this.state = {
      thisPeriod: "all",
      loading: true,
      loadingWaitingList: true,
      thisPosition: null,
      selectPosition: false,
      thisEducation: "all",
      thisGender: "all",
      searchPositionInput: "",
    };
  }

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setLoadingWaitingList = (value: boolean) => {
    this.setState({ loadingWaitingList: value });
  };

  setThisPosition = (value: PositionAPI | null) => {
    this.setState({ thisPosition: value });
  };

  setSelectPosition = (value: boolean) => {
    this.setState({ selectPosition: value });
  };

  setThisEducation = (value: education_category | "all") => {
    this.setState({ thisEducation: value });
  };

  setThisGender = (value: "male" | "female" | "all") => {
    this.setState({ thisGender: value });
  };

  setSearchPositionInput = (value: string) => {
    this.setState({ searchPositionInput: value });
  };

  setThisPeriod = (value: string) => {
    this.setState({ thisPeriod: value });
  };

  componentDidMount = () => {
    // LOADING EDUCATION CATEGORY =================================
    if (
      this.props.schoolLevels.education_categories === null ||
      this.props.schoolLevels.education_categories.length === 0
    ) {
      this.props.loadEducationCategories(this.setLoading);
    }
    // END LOADING EDUCATION CATEGORY =================================
    // ###################################################################
    // LOADING APPLICATION SESSION ====================================
    if (this.props.application_sessions == null) {
      this.props.GetAllSessionPlans((status: boolean, msg: string) => {
        this.setLoading(status);
        if (!status) {
          if (msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      });
    }
    // ###################################################################
    // LOADING POSITIONS ====================================
    if (
      this.props.systemPositions === null &&
      this.props.auth.education_institution_id
    ) {
      this.props.FC_GetPositionsByInstitution(
        this.props.auth.education_institution_id,
        (status: boolean, msg: string) => {
          this.setLoading(status);
          if (msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else if (this.state.loading) {
      this.setLoading(false);
    }
    // ###################################################################
    // LOADING WAITING LIST ====================================
    if (this.state.loadingWaitingList) {
      this.props.getWaitingList((status: boolean, msg: string) => {
        if (status === false) {
          this.setLoadingWaitingList(status);
          if (msg.length > 0) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      });
    }
  };

  FC_WaitingListStatus = (
    WaitingList: WaitingListDataInterface[]
  ): WaitingListChartDataInterface[] => {
    let output: WaitingListChartDataInterface[] = [
      {
        title: "All applicants",
        count: {
          all: WaitingList,
          waiting: WaitingList.filter(
            (item) => item.waiting_status === WaitingListStatus.WAITING
          ),
          placed: WaitingList.filter(
            (item) => item.waiting_status === WaitingListStatus.PLACED
          ),
          failed: WaitingList.filter(
            (item) => item.waiting_status === WaitingListStatus.PENDING
          ),
        },
        type: "all",
      },
      {
        title: "Male applicants",
        count: {
          all: WaitingList.filter(
            (item) => item.gender === applicantGender.male
          ),
          waiting: WaitingList.filter(
            (item) =>
              item.gender === applicantGender.male &&
              item.waiting_status === WaitingListStatus.WAITING
          ),
          placed: WaitingList.filter(
            (item) =>
              item.gender === applicantGender.male &&
              item.waiting_status === WaitingListStatus.PLACED
          ),
          failed: WaitingList.filter(
            (item) =>
              item.gender === applicantGender.male &&
              item.waiting_status === WaitingListStatus.PENDING
          ),
        },
        type: "male",
      },
      {
        title: "Female applicants",
        count: {
          all: WaitingList.filter(
            (item) => item.gender === applicantGender.female
          ),
          waiting: WaitingList.filter(
            (item) =>
              item.gender === applicantGender.female &&
              item.waiting_status === WaitingListStatus.WAITING
          ),
          placed: WaitingList.filter(
            (item) =>
              item.gender === applicantGender.female &&
              item.waiting_status === WaitingListStatus.PLACED
          ),
          failed: WaitingList.filter(
            (item) =>
              item.gender === applicantGender.female &&
              item.waiting_status === WaitingListStatus.PENDING
          ),
        },
        type: "female",
      },
    ];
    return output;
  };

  FC_WaitingListData = (
    allWaitList: WaitingListInterface[] | null,
    systemPositions: PositionAPI[] | null
  ): WaitingListDataInterface[] | null => {
    if (
      allWaitList === null ||
      systemPositions === null ||
      this.props.application_sessions === null
    )
      return null;
    let arrayNew: WaitingListDataInterface[] = [];
    // LOOP IN ALL WAITING LIST ONE BY ONE ###########################
    for (const key in allWaitList) {
      if (allWaitList[key].user_info) {
        const element = allWaitList[key];
        const thisPosition = getPositionById(
          systemPositions,
          element.position_id
        );
        const thisSession = getPeriodById(
          this.props.application_sessions,
          element.session_id
        );
        let dataArray: WaitingListDataInterface = {
          names: element.user_info.fname + " " + element.user_info.lname,
          waitingList_id: element._id,
          gender:
            element.user_info.sex === "M"
              ? applicantGender.male
              : applicantGender.male,
          national_id: element.n_id,
          phone: element.phone_number,
          email: element.email,
          user_position_marks: Number(element.marks.toFixed(2)),
          position_id: thisPosition
            ? thisPosition.position_id
            : "Unknown position",
          position_title: thisPosition
            ? thisPosition.position_title
            : "Unknown position",
          education_category: thisPosition
            ? thisPosition.education_institute_id.education_category_title
            : "",
          education_category_id: thisPosition
            ? thisPosition.education_institute_id.education_institute_id
            : "",
          waitingList_session: thisSession
            ? thisSession.session_plan_name
            : "Unknown session",
          date: DATE(element.createdAt),
          waiting_status: element.status,
          status: element.status,
          waitingList_session_id: element.session_id,
        };
        arrayNew.push(dataArray);
      }
    }
    return arrayNew;
  };

  FC_FilterWaitingList = (
    allWaitList: WaitingListDataInterface[],
    waitingListStatus: WaitingListStatus | "all",
    thisSession: string,
    institute: education_category | "all",
    thisPosition: PositionAPI | null
  ): WaitingListDataInterface[] => {
    let result: WaitingListDataInterface[] = allWaitList;
    if (waitingListStatus !== "all") {
      result = result.filter((item) => item.status === waitingListStatus);
    }

    if (thisPosition) {
      result = result.filter(
        (item) => item.position_id === thisPosition.position_id
      );
    }

    if (thisSession !== "all") {
      result = result.filter(
        (item) => item.waitingList_session_id === thisSession
      );
    }

    if (institute !== "all") {
      result = result.filter(
        (item) => item.education_category === institute.education_category_title
      );
    }
    return result;
  };

  getWaitingListDataByTitle = (
    allData: WaitingListChartDataInterface[]
  ): WaitingListChartDataInterface | undefined => {
    return allData.find((item) => item.type === this.state.thisGender);
  };

  displayGenderTitle = (gender: "male" | "female" | "all"): string => {
    if (gender === "male") {
      return "All male applicants";
    }
    if (gender === "female") {
      return "All female applicants";
    }
    return "All applicants";
  };

  render() {
    if (this.state.loading || this.props.application_sessions === null) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading Data...
        </div>
      );
    }

    if (this.props.application_sessions.length === 0) {
      return <NoSession />;
    }

    return (
      <div>
        {/* HEADER TITLE */}
        <WaitingListBoardTitle
          education_categories={this.props.schoolLevels.education_categories}
          loading={this.state.loading}
          loadingEducation={this.state.loading}
          thisEducation={this.state.thisEducation}
          setThisEducation={this.setThisEducation}
          thisPeriod={this.state.thisPeriod}
          setThisPeriod={this.setThisPeriod}
          application_sessions={this.props.application_sessions}
          reportTitle={"Waiting list Dashboard"}
          allEducation={true}
          setSelectPosition={this.setSelectPosition}
          thisPosition={this.state.thisPosition}
        />
        {/* END HEADER TITLE */}

        {/* ------------------------------------------------------------------------------ */}

        {(this.state.loadingWaitingList || this.props.waitingList === null) && (
          <section>
            <LoadingLabels />
          </section>
        )}

        {/* ------------------------------------------------------------------------------ */}

        {!this.state.loadingWaitingList && this.props.waitingList !== null && (
          <section>
            {/* WAITING LIST ALL STATUS LABELS ======================================= */}
            {this.FC_WaitingListData(
              this.props.waitingList,
              this.props.systemPositions
            ) &&
              this.FC_FilterWaitingList(
                this.FC_WaitingListData(
                  this.props.waitingList,
                  this.props.systemPositions
                )!,
                "all",
                this.state.thisPeriod,
                this.state.thisEducation,
                this.state.thisPosition
              ) && (
                <section className="my-2">
                  <div className="text-2xl font-semibold text-gray-600 flex items-center gap-2">
                    {this.displayGenderTitle(this.state.thisGender)}{" "}
                    <select
                      className={`btn bg-white border border-blue-300 text-gray-600 hover:bg-gray-50 text-sm focus:outline-none`}
                      value={this.state.thisGender}
                      onChange={(e) =>
                        this.setThisGender(
                          e.target.value as "male" | "female" | "all"
                        )
                      }
                    >
                      {this.FC_WaitingListStatus(
                        this.FC_FilterWaitingList(
                          this.FC_WaitingListData(
                            this.props.waitingList,
                            this.props.systemPositions
                          )!,
                          "all",
                          this.state.thisPeriod,
                          this.state.thisEducation,
                          this.state.thisPosition
                        )
                      ).map((item, index: number) => (
                        <option key={item.type + index} value={item.type}>
                          {item.title}
                        </option>
                      ))}
                    </select>
                  </div>
                  {this.getWaitingListDataByTitle(
                    this.FC_WaitingListStatus(
                      this.FC_FilterWaitingList(
                        this.FC_WaitingListData(
                          this.props.waitingList,
                          this.props.systemPositions
                        )!,
                        "all",
                        this.state.thisPeriod,
                        this.state.thisEducation,
                        this.state.thisPosition
                      )
                    )
                  ) && (
                    <ExamDashboardLabels
                      dataLabels={[
                        {
                          title: "Total applicants",
                          icon: FaBookOpen,
                          iconColor: "blue",
                          count: this.getWaitingListDataByTitle(
                            this.FC_WaitingListStatus(
                              this.FC_FilterWaitingList(
                                this.FC_WaitingListData(
                                  this.props.waitingList,
                                  this.props.systemPositions
                                )!,
                                "all",
                                this.state.thisPeriod,
                                this.state.thisEducation,
                                this.state.thisPosition
                              )
                            )
                          )!.count.all.length,
                        },
                        {
                          title: "Waiting Applicants",
                          icon: FaBookOpen,
                          iconColor: "green",
                          count: this.getWaitingListDataByTitle(
                            this.FC_WaitingListStatus(
                              this.FC_FilterWaitingList(
                                this.FC_WaitingListData(
                                  this.props.waitingList,
                                  this.props.systemPositions
                                )!,
                                "all",
                                this.state.thisPeriod,
                                this.state.thisEducation,
                                this.state.thisPosition
                              )
                            )
                          )!.count.waiting.length,
                        },
                        {
                          title: "Placed & Not confirmed",
                          icon: FaBookOpen,
                          iconColor: "yellow",
                          count: this.getWaitingListDataByTitle(
                            this.FC_WaitingListStatus(
                              this.FC_FilterWaitingList(
                                this.FC_WaitingListData(
                                  this.props.waitingList,
                                  this.props.systemPositions
                                )!,
                                "all",
                                this.state.thisPeriod,
                                this.state.thisEducation,
                                this.state.thisPosition
                              )
                            )
                          )!.count.failed.length,
                        },
                        {
                          title: "Placed & Confirmed",
                          icon: FaBookOpen,
                          iconColor: "gray",
                          count: this.getWaitingListDataByTitle(
                            this.FC_WaitingListStatus(
                              this.FC_FilterWaitingList(
                                this.FC_WaitingListData(
                                  this.props.waitingList,
                                  this.props.systemPositions
                                )!,
                                "all",
                                this.state.thisPeriod,
                                this.state.thisEducation,
                                this.state.thisPosition
                              )
                            )
                          )!.count.placed.length,
                        },
                      ]}
                      labelsOnRow={5}
                    >
                      <div className="flex justify-center">
                        <PieChart
                          options={{
                            labels: [
                              "Waiting list",
                              "Placed list",
                              "Failed exam",
                            ],
                            legend: {
                              show: false,
                              position: "bottom",
                              horizontalAlign: "center",
                              fontSize: "16px",
                              itemMargin: {
                                horizontal: 3,
                                vertical: 0,
                              },
                            },
                            plotOptions: {
                              pie: {
                                donut: {
                                  labels: {
                                    show: true,
                                    name: {
                                      show: true,
                                      fontSize: "16px",
                                      //   fontFamily: "Rubik",
                                      color: "#2564eb",
                                      offsetY: -10,
                                    },
                                    value: {
                                      show: true,
                                      fontSize: "20px",
                                      fontFamily:
                                        "Helvetica, Arial, sans-serif",
                                      color: "#2564eb",
                                      offsetY: 10,
                                      formatter: function (val) {
                                        return val;
                                      },
                                    },
                                    total: {
                                      show: true,
                                      label: "Total",
                                      color: "#2564eb",
                                      formatter: function (w) {
                                        return w.globals.seriesTotals.reduce(
                                          (a: number, b: number) => {
                                            return a + b;
                                          },
                                          0
                                        );
                                      },
                                    },
                                  },
                                },
                              },
                            },
                            colors: ["#469769", "#d97724", "#4b5563"],
                          }}
                          series={[
                            this.getWaitingListDataByTitle(
                              this.FC_WaitingListStatus(
                                this.FC_FilterWaitingList(
                                  this.FC_WaitingListData(
                                    this.props.waitingList,
                                    this.props.systemPositions
                                  )!,
                                  "all",
                                  this.state.thisPeriod,
                                  this.state.thisEducation,
                                  this.state.thisPosition
                                )
                              )
                            )!.count.waiting.length,
                            this.getWaitingListDataByTitle(
                              this.FC_WaitingListStatus(
                                this.FC_FilterWaitingList(
                                  this.FC_WaitingListData(
                                    this.props.waitingList,
                                    this.props.systemPositions
                                  )!,
                                  "all",
                                  this.state.thisPeriod,
                                  this.state.thisEducation,
                                  this.state.thisPosition
                                )
                              )
                            )!.count.placed.length,
                            this.getWaitingListDataByTitle(
                              this.FC_WaitingListStatus(
                                this.FC_FilterWaitingList(
                                  this.FC_WaitingListData(
                                    this.props.waitingList,
                                    this.props.systemPositions
                                  )!,
                                  "all",
                                  this.state.thisPeriod,
                                  this.state.thisEducation,
                                  this.state.thisPosition
                                )
                              )
                            )!.count.failed.length,
                          ]}
                          width={210}
                        />
                      </div>
                    </ExamDashboardLabels>
                  )}
                </section>
              )}
            {/* WAITING LIST STATUS ON COUNTRY LEVEL LABELS ======================================= */}

            {/* ===================================================================================== */}

            {/* EXAM PERFORMANCE ON COUNTRY CHART ======================================= */}
            {this.FC_WaitingListData(
              this.props.waitingList,
              this.props.systemPositions
            ) && (
              <WaitingListChart
                waitingListData={this.FC_FilterWaitingList(
                  this.FC_WaitingListData(
                    this.props.waitingList,
                    this.props.systemPositions
                  )!,
                  "all",
                  this.state.thisPeriod,
                  this.state.thisEducation,
                  this.state.thisPosition
                )}
                sessions={this.props.application_sessions}
                chartHeight={650}
                thisGender={this.state.thisGender}
              />
            )}
            {/* END EXAM PERFORMANCE ON COUNTRY CHART ======================================= */}

            {/* CHOOSING POSITION MODAL */}
            {this.state.selectPosition && (
              <Modal
                title={
                  <div className="flex flex-row gap-4 items-center">
                    <IoFilter className="text-5xl text-blue-600" />
                    <div className="flex flex-col">
                      <span className="font-bold text-xl text-blue-600">
                        Filter by a position
                      </span>
                    </div>
                  </div>
                }
                backDrop={true}
                theme={Themes.default}
                close={() => {
                  this.setSelectPosition(false);
                  this.setSearchPositionInput("");
                }}
                backDropClose={true}
                widthSizeClass={ModalSize.extraLarge}
                displayClose={true}
                padding={{ title: true }}
              >
                <div className="px-2 pb-2">
                  {this.props.systemPositions === null ? (
                    <LoadingV2 type={LoadingType.dark} />
                  ) : this.props.systemPositions.length === 0 ? (
                    <div className="flex flex-col text-gray-400 items-center justify-center">
                      <MdSearch className="text-6xl" />
                      <div className="flex flex-col items-center justify-center">
                        <span>No positions found! Please go back!</span>
                        {/* <button
                        onClick={() => this.loadPosition()}
                        className={`btn bg-yellow-100 text-yellow-700 my-4 hover:bg-yellow-200 ${
                          this.state.loadingPosition && "animate-pulse"
                        }`}
                        disabled={this.state.loadingPosition}
                      >
                        {this.state.loadingPosition ? "Loading..." : "Reload"}
                      </button> */}
                      </div>
                    </div>
                  ) : (
                    <>
                      <div className="flex flex-col">
                        <span className="text-gray-600 text-xs font-bold">
                          Filter by position name
                        </span>
                        <input
                          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                            this.setSearchPositionInput(e.target.value)
                          }
                          type="text"
                          className="mb-4 bg-gray-100 px-3 py-2 rounded"
                          placeholder="Keyword..."
                        />
                      </div>
                      <div
                        onClick={() => {
                          this.setThisPosition(null);
                          this.setSelectPosition(false);
                          this.setSearchPositionInput("");
                        }}
                        className="bg-white cursor-pointer rounded  px-3 py-2 border border-gray-200 hover:border-blue-500 hover:bg-blue-100 mb-2"
                      >
                        <div className="grid lg:grid-cols-12">
                          <div className="col-span-12 lg:col-span-10">
                            <div className="flex flex-row items-center gap-3">
                              <div>
                                <IoIosBriefcase className="text-4xl md:text-5xl text-blue-600" />
                              </div>
                              <div className="flex flex-col">
                                <span className="text-base lg:text-lg font-bold text-gray-700">
                                  All position
                                </span>
                              </div>
                            </div>
                          </div>
                          <div className="col-span-12 lg:col-span-2 hidden lg:block">
                            <div className="h-full flex items-center justify-end">
                              <div className="btn bg-blue-600 text-white float-right">
                                Choose
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      {search(
                        this.props.systemPositions,
                        this.state.searchPositionInput
                      ).map((item: PositionAPI, key: number) => (
                        <div
                          key={key + 1}
                          onClick={() => {
                            this.setThisPosition(item);
                            this.setSelectPosition(false);
                            this.setSearchPositionInput("");
                          }}
                          className="bg-white cursor-pointer rounded  px-3 py-2 border border-gray-200 hover:border-blue-500 hover:bg-blue-100 mb-2"
                        >
                          <div className="grid lg:grid-cols-12">
                            <div className="col-span-12 lg:col-span-10">
                              <div className="flex flex-row items-center gap-3">
                                <div>
                                  <IoIosBriefcase className="text-4xl md:text-5xl text-blue-600" />
                                </div>
                                <div className="flex flex-col">
                                  <span className="text-xs lg:text-sm font-bold text-gray-700">
                                    {item.position_title}
                                  </span>
                                  <span className="text-xs text-gray-500 italic">
                                    School level:{" "}
                                    <span
                                      className="text-gray-800 font-light"
                                      style={{ fontStyle: "normal" }}
                                    >
                                      {item.school_level_id.school_level_name}
                                    </span>
                                  </span>
                                  <span className="text-xs font-bold text-blue-600 bg-blue-100 w-max px-3 py-1 rounded-full">
                                    {
                                      item.education_institute_id
                                        .education_category_title
                                    }
                                  </span>
                                </div>
                              </div>
                            </div>
                            <div className="col-span-12 lg:col-span-2 hidden lg:block">
                              <div className="h-full flex items-center justify-end">
                                <div className="btn bg-blue-600 text-white float-right">
                                  Choose
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      ))}
                    </>
                  )}
                </div>
              </Modal>
            )}
            {/* END CHOOSING POSITION MODAL */}
          </section>
        )}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  system_settings,
  sessionPlan,
  schoolLevels,
  waitingList,
}: StoreState): {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  systemPositions: PositionAPI[] | null;
  schoolLevels: schoolLevelsStoreInterface;
  waitingList: WaitingListInterface[] | null;
} => {
  return {
    auth: auth,
    application_sessions: sessionPlan.allSessionPlans,
    systemPositions: system_settings.systemPositions,
    schoolLevels,
    waitingList,
  };
};

export const WaitingListDashboard = connect(mapStateToProps, {
  getWaitingList,
  GetAllSessionPlans,
  loadEducationCategories,
  FC_GetPositionsByInstitution,
  PushPopupAlert,
})(_WaitingListDashboard);
