import React, { Component } from "react";
import { connect } from "react-redux";
import {
  FC_getLocations,
  FC_LoadSchoolsByDistrict,
  FC_LoadSchoolsByIDs,
  FT_LoadSchoolsByDistrict,
  FT_LoadSchoolsByIDs,
  LocationAPI,
  SchoolContract,
} from "../../actions";
import {
  FC_GetWaitingLocationList,
  FT_GetWaitingLocationList,
} from "../../actions/location";
import { ContractItemList } from "../../containers/PlacementContract/Contract/Contract";
import { ViewContractType } from "../../interfaces/waitingList";
import { StoreState } from "../../reducers";
import Loading from "../Loading/Loading";
import DistrictView from "./DistrictView";
import ProvinceView from "./ProvinceView";
import SchoolView from "./SchoolView";
import SectorView from "./SectorView";

interface ContractViewerProps {
  // FROM PROPS
  candidates: string;
  position: string;
  contract: ViewContractType;

  // FROM REDUX =========
  locations: LocationAPI | null;
  FC_LoadSchoolsByDistrict: FT_LoadSchoolsByDistrict;
  FC_LoadSchoolsByIDs: FT_LoadSchoolsByIDs;
  FC_GetWaitingLocationList: FT_GetWaitingLocationList;
}

interface ContractViewerState {
  loading: boolean;
  selectedSchool: SchoolContract[] | null;
}

class ContractViewer extends Component<
  ContractViewerProps,
  ContractViewerState
> {
  constructor(props: ContractViewerProps) {
    super(props);

    this.state = {
      loading: true,
      selectedSchool: null,
    };
  }

  componentDidMount = () => {
    if (this.props.locations === null)
      FC_getLocations(
        (status: boolean, data: LocationAPI | null, msg: string) => {
          if (status === true && data !== null) {
            this.props.FC_GetWaitingLocationList(data);
            this.setState({
              loading: false,
            });
          } else {
            this.setState({
              loading: false,
            });
          }
        }
      );
    // else this.setState({ loading: false });
    if (
      this.state.selectedSchool === null &&
      this.props.contract.school.values.length >= 1
    ) {
      this.setState({
        loading: true,
      });
      this.props.FC_LoadSchoolsByIDs(
        this.props.contract.school.values.map((itm) => itm.toString()),
        (status, schools, err) => {
          if (status === true) {
            this.setState({
              selectedSchool: schools,
              loading: false,
            });
          }
        }
      );
    } else this.setState({ loading: false, selectedSchool: [] });
  };

  render() {
    if (
      this.state.loading === true ||
      this.props.locations === null ||
      this.state.selectedSchool === null
    ) {
      return (
        <div className="bg-white mt-6 mx-6 p-6 h-full flex flex-col items-start justify-center rounded">
          <div className="text-xl mx-4 my-4 animate-pulse">
            Loading contract, Please wait...
          </div>
          <Loading />
        </div>
      );
    }

    let ProvincesList: ContractItemList[] = [];
    let DistrictList: ContractItemList[] = [];
    let SectorList: ContractItemList[] = [];

    if (this.props.contract !== null) {
      // * Province
      this.props.contract.province.values.forEach((el) => {
        if (this.props.locations !== null) {
          let findIt = this.props.locations.provinces.find(
            (element) => element.province_code === el.toString()
          );
          if (findIt !== undefined) {
            ProvincesList.push({
              id: findIt.province_code,
              title: findIt.province_name,
            } as ContractItemList);
          }
        }
      });

      // * Province
      this.props.contract.district.values.forEach((el) => {
        if (this.props.locations !== null) {
          let findIt = this.props.locations.districts.find(
            (element) => element.district_code === el.toString()
          );
          if (findIt !== undefined) {
            DistrictList.push({
              id: findIt.district_code,
              title: findIt.district_name,
            } as ContractItemList);
          }
        }
      });

      // * Sector
      this.props.contract.sector.values.forEach((el) => {
        if (this.props.locations !== null) {
          let findIt = this.props.locations.sectors.find(
            (element) => element.sector_code === el.toString()
          );
          if (findIt !== undefined) {
            SectorList.push({
              id: findIt.sector_code,
              title: findIt.sector_name,
            } as ContractItemList);
          }
        }
      });
    }

    const thisCandidate: JSX.Element = (
      <span className="font-semibold">{this.props.candidates}</span>
    );
    const thisPosition: JSX.Element = (
      <span className="font-semibold">{this.props.position}</span>
    );

    return (
      <div className="pb-4">
        <div className="px-4">
          <h2 className="text-black text-center font-semibold text-2xl sm:text-3xl underline">
            Placement Preferences
          </h2>

          <div className="mt-6 text-lg flex flex-col">
            <p>Names: {thisCandidate}</p>
            <p>Position: {thisPosition}</p>
          </div>

          {/* <p className="mt-5">
            This preferences were made by {thisCandidate} on {thisPosition}, by
            Tempor in laborum culpa et consectetur elit occaecat cupidatat est
            voluptate tempor deserunt non ea.
          </p> */}
        </div>

        <ProvinceView
          data={ProvincesList}
          strict={this.props.contract.province.strict}
        />

        <DistrictView
          data={DistrictList}
          strict={this.props.contract.district.strict}
        />

        <SectorView
          data={SectorList}
          strict={this.props.contract.sector.strict}
        />

        <SchoolView
          data={this.state.selectedSchool.map((itm) => ({
            id: itm.school_code,
            title: itm.school_name,
          }))}
          strict={this.props.contract.school.strict}
        />
      </div>
    );
  }
}

const mapStateToProps = ({
  locations,
}: StoreState): {
  locations: LocationAPI | null;
} => {
  return { locations: locations.waitingLocation };
};
export default React.memo(
  connect(mapStateToProps, {
    FC_LoadSchoolsByDistrict,
    FC_LoadSchoolsByIDs,
    FC_GetWaitingLocationList,
  })(ContractViewer)
);
