import React from "react";
import { FaAngleDoubleRight } from "react-icons/fa";
import { ContractItemList } from "../../containers/PlacementContract/Contract/Contract";

interface AppProps {
  data: ContractItemList[];
  strict: boolean;
  //   expandClick?: () => void;
  //   loading: boolean;
}

const DistrictView: React.FC<AppProps> = (props) => {
  const displayMessage = (): JSX.Element => {
    let result: JSX.Element = <></>;
    let districts: string = "";
    if (props.data.length === 0)
      result = (
        <p>
          The system will place me in the available posts from all districts of
          the selected provinces.
        </p>
      );
    else if (props.strict === false && props.data.length > 0) {
      props.data.forEach((district, index) => {
        districts += district.title;
        if (index <= props.data.length - 1) districts += ", ";
      });

      result = (
        <p>
          The system will place you in the available posts{" "}
          <span className="font-semibold">starting</span> from{" "}
          <span className="font-semibold">{districts}</span> and all remaining
          districts of the selected provinces.
        </p>
      );
    } else if (props.strict === true && props.data.length > 0) {
      props.data.forEach((district, index) => {
        districts += district.title;
        if (index <= props.data.length - 2) districts += ", ";
        if (index === props.data.length - 2 && index > 0) districts += " and ";
      });

      result = (
        <p>
          The system will <span className="font-semibold">only</span> place me
          in available posts in{" "}
          <span className="font-semibold">{districts}</span>.
        </p>
      );
    }
    return result;
  };

  return (
    <div className="bg-white mt-9 px-4">
      <div className="flex flex-row items-center gap-1">
        <FaAngleDoubleRight />
        <span className={`font-extrabold text-lg`}>
          Preferences at district level
        </span>
      </div>

      <p>{displayMessage()}</p>
    </div>
  );
};

export default DistrictView;
