import React from "react";
import { AiFillDashboard } from "react-icons/ai";
import { BiFilterAlt } from "react-icons/bi";
import { MdBrightness1 } from "react-icons/md";
import { PositionAPI, SessionPlanInterface } from "../../../interfaces";
import { education_category } from "../../../interfaces/education_category";

type WaitingListBoardTitleProps = {
  reportTitle: string;
  education_categories: education_category[] | null;
  loading: boolean;
  loadingEducation: boolean;
  thisEducation: education_category | "all";
  allEducation: boolean;
  setThisEducation: (value: education_category | "all") => void;
  thisPeriod: string;
  setThisPeriod: (value: string) => void;
  setSelectPosition: (value: boolean) => void;
  application_sessions: SessionPlanInterface[];
  thisPosition: PositionAPI | null;
};

const WaitingListBoardTitle: React.FC<WaitingListBoardTitleProps> = (props) => {
  return (
    <div className="mt-3 rounded flex flex-col md:flex-row md:justify-between items-start">
      <section>
        <div className="flex flex-row items-center gap-3 mb-2">
          <AiFillDashboard className="text-7xl text-blue-600 hidden md:block" />
          <div className="flex flex-col">
            <span className="text-blue-600 text-3xl font-bold">
              {props.reportTitle}
            </span>
            <div
              onClick={() => props.setSelectPosition(true)}
              className="flex flex-row items-center gap-2 mt-1"
            >
              <span className="text-gray-500">Selected position: </span>
              <span className="text-gray-700 text-lg font-bold underline cursor-pointer">
                {props.thisPosition !== null
                  ? props.thisPosition.position_title
                  : "All positions"}
              </span>
              <button
                onClick={() => props.setSelectPosition(true)}
                className="btn bg-blue-50 border border-blue-300 text-blue-600 cursor-pointer hover:bg-blue-100 flex flex-row items-center justify-center gap-2 text-sm"
              >
                <BiFilterAlt className="text-xl" />
                Filter by position
              </button>
            </div>
          </div>
        </div>
      </section>

      {/* FILTER SECTION PERIOD && EDUCATION */}
      <section className="w-full md:w-auto my-2 md:my-0">
        {/* EDUCATION CATEGORY sections */}
        <section>
          {props.education_categories && props.education_categories.length > 0 && (
            <div className="flex items-start md:items-center justify-start md:justify-end gap-2">
              {props.allEducation && (
                <div
                  className={`w-auto ${
                    props.loading || props.loadingEducation
                      ? "cursor-not-allowed"
                      : "cursor-pointer"
                  } ${
                    props.thisEducation === "all"
                      ? "bg-blue-500 text-white font-bold"
                      : "bg-gray-100 text-blue-800"
                  } mb-1 hover:font-bold py-1 hover:bg-blue-400 hover:text-white --font-bold  px-2 rounded-md flex items-center animate__animated animate__zoomIn`}
                  onClick={() =>
                    !props.loading &&
                    !props.loadingEducation &&
                    props.setThisEducation("all")
                  }
                >
                  <section className="flex-1">
                    <MdBrightness1 className="inline-block mr-1 text-xs" />
                    <span>All</span>
                  </section>
                </div>
              )}
              {props.education_categories.map((education, index) => (
                <div
                  key={education.education_institute_id + index}
                  className={`w-auto ${
                    props.loading ? "cursor-not-allowed" : "cursor-pointer"
                  } ${
                    props.thisEducation !== "all" &&
                    education.education_institute_id ===
                      props.thisEducation.education_institute_id
                      ? "bg-blue-500 text-white font-bold"
                      : "bg-gray-100 text-blue-800"
                  } mb-1 hover:font-bold py-1 hover:bg-blue-400 hover:text-white --font-bold  px-2 rounded-md flex items-center animate__animated animate__zoomIn`}
                  onClick={() =>
                    !props.loading &&
                    !props.loadingEducation &&
                    props.setThisEducation(education)
                  }
                >
                  <div className="flex-1">
                    <MdBrightness1 className="inline-block mr-1 text-xs" />
                    <span>{education.education_category_title}</span>
                  </div>
                </div>
              ))}
            </div>
          )}
        </section>
        {/* end EDUCATION CATEGORY sections */}
        <select
          className={`bg-white text-gray-600 border border-gray-400 rounded p-2 focus:outline-none`}
          value={props.thisPeriod}
          disabled={props.loading || props.loadingEducation}
          onChange={(e) => {
            props.setThisPeriod(e.target.value);
          }}
        >
          <option value="all">All Session</option>
          {props.application_sessions.map((session, item) => (
            <option key={session._id + item} value={session._id}>
              {session.session_plan_name}
            </option>
          ))}
        </select>
      </section>
      {/* END FILTER SECTION PERIOD && EDUCATION */}
    </div>
  );
};

export default React.memo(WaitingListBoardTitle);
